import classes from "./Faq.module.css";

import photo from "../../assets/png/author.jpg";

import { useDispatch } from "react-redux";
import { faqActions } from "../../store/faq-slice";

const Faq = () => {
  const dispatch = useDispatch();

  const backdropHandler = () => {
    dispatch(faqActions.showFaq());
  };

  return (
    <>
      <div className={classes.modal} onClick={backdropHandler}>
        <div className={classes.container}>
          <div className={classes.photo}>
            <img src={photo} alt="zdjcie autora" />
          </div>
          <div className={classes.desc}>
            <h1>To moja wina...</h1>
            <p>
              Na pocztku miaem plan spisa wszystkie suchary Romana do
              notatnika, ale p贸藕niej pomylaem, 偶e skoro ucz si frontendu to
              mam fajny pomys na kolejn aplikacj :)
            </p>
            <div className={classes.link}>
              <a href="https://github.com/bialek-k">tyle narobiem</a>
            </div>
          </div>
        </div>
      </div>
      <div className={classes.backdrop}></div>
    </>
  );
};

export default Faq;
